import Brush from "./Brush";
import toolState from "../store/toolState";

export default class Eraser extends Brush {
    constructor(canvas: HTMLCanvasElement, socket: WebSocket, sessionId: string, username: string) {
        super(canvas, socket, sessionId, username)
    }
    mouseMoveHandler(e: MouseEvent) {
        if (this.mouseDown && e.target instanceof HTMLCanvasElement) {
            this.draw(e.pageX - e.target.offsetLeft, e.pageY - e.target.offsetTop)
            this.socket.send(JSON.stringify({
                method: 'draw',
                id: this.sessionId,
                username: this.username,
                figure: {
                    x: e.pageX - e.target.offsetLeft,
                    y: e.pageY - e.target.offsetTop,
                    lineWidth: this.ctx?.lineWidth,
                    strokeStyle: '#FFFFFF',
                    type: 'eraser'
                }
            }))
        }
    }
    mouseUpHandler(e: MouseEvent) {
        super.mouseUpHandler(e);

    }
    mouseDownHandler(e: MouseEvent) {
        super.mouseDownHandler(e);
    }
    draw(x: number, y: number) {
        if(this.ctx) {
            this.ctx.lineWidth = toolState.lineWidth
            this.ctx.strokeStyle = '#FFFFFF'
            this.ctx.lineTo(x, y)
            this.ctx.stroke()
        }
    }
    static draw(ctx: CanvasRenderingContext2D, x: number, y: number, figure: any) {
        const tempStyle = {
            lineWidth: ctx.lineWidth,
            strokeStyle: ctx.strokeStyle
        }
        ctx.lineWidth = figure.lineWidth
        ctx.strokeStyle = '#FFFFFF'
        ctx.lineTo(x, y)
        ctx.stroke()
        ctx.strokeStyle = tempStyle.strokeStyle
        ctx.lineWidth = tempStyle.lineWidth
    }
}